import { Table, Modal } from 'antd';
import ProgramListComponent from '../programList/ProgramListComponent';
import ThemeDetailComponent from '../themeDetail/ThemeDetailComponent';

const confirm = Modal.confirm;

export default class ThemeSearchListComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: false,//pop
      detail_visible: false,//pop
      theme_id:-1,
      data:[],//
      data_themedetail:[],//
      data_programs:[]//

    };

  }
  confirm() {
    confirm({
        title: '?!!',
        content: ', 1s',
        onOk() {
          return new Promise((resolve, reject) => {
            setTimeout(Math.random() > 0.5 ? resolve : reject, 1000);
          }).catch(() => console.log('Oops errors!'));
        },
        onCancel() {}
    });
  }
  confirmOffline() {
    confirm({
        title: '?',
        content: ', 1s',
        onOk() {
          return new Promise((resolve, reject) => {
            setTimeout(Math.random() > 0.5 ? resolve : reject, 1000);
          }).catch(() => console.log('Oops errors!'));
        },
        onCancel() {}
    });
  }
  themeContainer(){

  }
  showProgramModal(id) {
    //
    let data = [];
    this.setState({
      visible: true,
      theme_id: id,
      data_programs:data
    });
  }
  showDetailModal(id) {
    //
    let data = [];
    this.setState({
      detail_visible: true,
      theme_id: id,
      data_themedetail:data
    });
  }
  handleOk() {
    this.setState({
      ModalText: '2s',
      confirmLoading: true,
    });
    setTimeout(() => {
      this.setState({
        visible: false,
        confirmLoading: false,
      });
    }, 2000);
  }
  handleCancel() {
    console.log('Clicked cancel button');
    this.setState({
      visible: false,
    });
  }
  handleDetailOk() {
    this.setState({
      ModalText: '2s',
      confirmLoading: true,
    });
    setTimeout(() => {
      this.setState({
        detail_visible: false,
        detail_confirmLoading: false,
      });
    }, 2000);
  }
  handleDetailCancel() {
    console.log('Clicked cancel button');
    this.setState({
      detail_visible: false,
    });
  }
  componentWillMount(){
    //let data = this.props.data;
    let data = [{
      id: '121',
      theme: '',
      type: '',
      from: '',
      rule: '',
      contains:'1223',
      author:'aa',
      status:''
    }, {
      id: '1222',
      theme: '',
      type: '',
      from: '',
      rule: '',
      contains:'1223',
      author:'aa',
      status:''
    }, {
      id: '123',
      theme: '',
      type: '',
      from: '',
      rule: '',
      contains:'1223',
      author:'aa',
      status:''
    }];
    this.setState({'data':data});
  }
  render() {
    let columns = [{
      title: 'ID',
      dataIndex: 'id',
      key: 'id'
    }, {
      title: '',
      dataIndex: 'theme',
      key: 'theme',
      render: (text,record) => <a href='#' onClick={() => this.showDetailModal(record.id)}>{record.id}{text}</a>
    }, {
      title: '',
      dataIndex: 'type',
      key: 'type'
    },{
      title: '',
      dataIndex: 'from',
      key: 'from'
    },{
      title: '',
      dataIndex: 'rule',
      key: 'rule'
    },{
      title: '',
      dataIndex: 'contains',
      key: 'contains',
      render: (text,record) => <a href='#' onClick={() => this.showProgramModal(record.id)}>{text}</a>
    },{
      title: '',
      dataIndex: 'author',
      key: 'author',
    },{
      title: '',
      dataIndex: 'status',
      key: 'status',
    }, {
      title: '',
      key: 'action',
      render: (text, record) => (
        <span>
          <a href='#'></a>
          <span className='ant-divider' />
          <a href='#' onClick={this.confirmOffline}></a>
          <span className='ant-divider' />
          <a onClick={this.confirm}></a>
        </span>
      ),
    }];


    return (
      
        <div>
          <Table columns={columns} dataSource={this.state.data} className='theme-list'/>
          <Modal wrapClassName='program-list-wrap' title='' width={'96%'}
                 visible={this.state.visible}
                 onOk={() => this.handleOk()}
                 confirmLoading={this.state.confirmLoading}
                 onCancel={() => this.handleCancel()}>
                    <ProgramListComponent  data={this.state.data_programs} themeid={this.state.theme_id}/>
          </Modal>
          <Modal wrapClassName='theme-list-wrap' title='' width={'96%'}
                 visible={this.state.detail_visible}
                 onOk={() => this.handleDetailOk()}
                 confirmLoading={this.state.detail_confirmLoading}
                 onCancel={() => this.handleDetailCancel()}>
                    <ThemeDetailComponent data={this.state.data_themedetail} themeid={this.state.theme_id}/>
          </Modal>
        </div>
    );

  }
}






